@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap);
body {
    background-image: linear-gradient(to right top, #ffa3b3, #f9a8c4, #f1aed3, #e6b4df, #dabbe8, #cec3f3, #c0cafb, #b2d2ff, #98ddff, #7ae8ff, #62f3ff, #5ffbf1);
    background-attachment: fixed;
    font-family: 'Quicksand', sans-serif;
    color: #354538;
}

li {
    list-style-type: circle;
}

ul {
    padding-left: 30px;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background-image: linear-gradient(to right bottom,#c332f1, #ee71bb);
    border-radius: 10px;
}


a {
    text-decoration: none;
    color: #354548;
}

a:hover {
    text-decoration: none;
    color: black;
}

.circle1,.circle2 {

    background-color: #fff;
    background: rgba( 255, 255, 255, 0.3 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;

    position: absolute;
    border-radius: 50%;
}

.circle1 {
    top: 0.009%;
    right: 0.009%;
    height: 16rem;
    width: 16rem;
    
}

.circle2 {
    bottom: 1%;
    left: 15%;
    height: 15rem;
    width: 15rem;

}

.app__container {
    padding-top: 35px;
}

.app__row {
    height: 86vh;
}


.app__main-content {
    background-color: #fff;
    border-radius: 20px;
    padding: 25px 15px;
    box-shadow: 0 0 10px black;

    background: rgba( 255, 255, 255, 0.40 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 5.5px );
    -webkit-backdrop-filter: blur( 5.5px );
    border-radius: 10px;

    min-height: 90vh;
}

/* Side Bar */
.sidebar {
    background-color: #fff;
    height: 100%;
    border-radius: 20px;
    padding: 40px 15px;
    box-shadow: 0 0 5px grey;
    text-align: center;

    background: rgba( 255, 255, 255, 0.20 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 5.5px );
    -webkit-backdrop-filter: blur( 5.5px );
    border-radius: 10px;

}

.sidebar__avatar {
    height: 120px;
    width: 120px;
    object-fit: contain;
    border-radius: 30px;
    
}

.sidebar__icon {
    width: 25px;
    height: 25px;
}

.sidebar__name {
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    margin: 20px 0;
    font-size: 1.7rem;
    color: #354358;
}


.sidebar__item {
    margin-bottom: 6px;
    padding-top: 6px;
    height: 40px;
    font-size: 1.05rem;
    border-radius: 30px;
}

.sidebar_item__title {
    border-radius: 30px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    font-size: 1.10rem;
}

.sidebar__contact {
    padding: 2px 0;
    margin: 20px -15px;
}

.sidebar__github {
    /* margin-right: 7px; */
    padding-right: 10px;
    font-family: 'Quicksand', sans-serif;
    color: #354358;


    margin: 20px 0;
}

.sidebar__linkedin {
    font-family: 'Quicksand', sans-serif;
    color: #354358;


    margin: 20px 0;
    
}

.sidebar__info {
    margin-top: 30px;
}

.sidebar__email {
    background-image: linear-gradient(to right bottom,#c332f1, #ee71bb);
    border-radius: 30px;
    color: #fff;
    /* margin: 15px 25px 0px; */
    cursor: pointer;
    transition: all 0.05s linear;
    font-family: 'Quicksand', sans-serif;

    margin: 20px 25px 20px;
}

.sidebar__email:hover {
    transform: scale(1.1);
}

.sidebar__resume {
    background-image: linear-gradient(to right bottom,#c332f1, #ee71bb);
    border-radius: 30px;
    color: #fff;
    /* margin: 15px 25px 0px; */
    cursor: pointer;
    transition: all 0.05s linear;
    font-family: 'Quicksand', sans-serif;

    margin: 20px 25px 20px;

    
    
}

.sidebar__resume:hover {
    transform: scale(1.1);
}

.sidebar__linkedin:hover,
.sidebar__github:hover {
    color: #000000;
    background: rgba( 255, 255, 255, 0.40 );
    border-radius: 10px;

}

@media(max-width:991px) {
    .app__main-content{
        margin: 30px 0;
    }
}



/* NavBar */

.navbar__items {
    display: flex;
}

.navbar__active {
    font-size: 2rem;
    font-weight: 700;
    font-family: 'Quicksand', sans-serif;
    color: #354358; 
    border-bottom: 4px solid #c332f1;
}

.navbar__item {
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    margin-right: 20px;
    text-decoration: none;
    color:#354358;
    font-size: 1.3rem;
    cursor: pointer;

}

.navbar__item:hover{
    background: -webkit-linear-gradient(#c332f1, #ee71bb);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    
}

/* About */

.about {
    padding: 20px;
    margin: -35px;
}

.about__intro {
    font-size: 1.1rem;
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;
    padding: 10px 30px;
    margin: 10px 0;
    margin-bottom: 0;
}

.about__container {
    /* background-color: #dbdddf; */
    padding: 10px 20px;
}

.about__heading {
    font-size: 20px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    color: #354358; 
    margin-left: 10px;
}

/* Skill Card */
.skill-card {
    margin: 5px;
    padding: 5px; 

    display: flex;
    flex-direction: row;
    border-radius: 10px;

    background: rgba( 255, 255, 255, 0.40 );
    border-radius: 10px;
}

.skill-card__icon {
    width: 20px;
    height: 20px;
    margin-left: 10px;
}

.skill-card-body {
    margin-left: 15px;
}

.skill-card__title {
    font-family: 'Quicksand', sans-serif;
    margin-bottom: 0;
}

/* Projects */
.projects {
    height: 72vh;
    padding-top: 30px;
    overflow-y: auto;
}

.projectCard__wrapper {
    margin: 10px 0;
    width: 100%;
    height: 100%;
    /* min-height: 160px; */
    max-height: 200px;
}

.projectCard__image {
    width: 100%;
    height: 80%;
    border-radius: 30px;
    /* border: 1px solid #354358; */
    transition: transform 0.05s linear;
}

.projectCard__image:hover {
    transform: scale(1.05);

    box-shadow: 3px 8px 10px 0px rgba( 31, 38, 135, 0.37 );
    background: rgba( 255, 255, 255, 0.40 );
}

.projectCard__icon {
    width: 25px;
    height: 25px;
    margin-right: 10px;
    margin-left: 10px;
}

.projectCard__title {
    margin-top: 12px;
    font-size: 1rem;
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
}

.projects__navbar {
    display: flex;
    margin-bottom: 5px;

}

.projects__navbar>div {
    margin: 0 10px;
    font-size: 1rem;
    cursor: pointer;
}


.projects__navbar-active {
    color:#c332f1;
}

/* Experience */
.experience {
    padding-top: 20px;
}

.experience-card {
    transition: transform 0.05s linear;
    margin-top: 5px;
    min-height: 67vh;
}

.experience-card:hover {
    color: #000000;
    background: rgba( 255, 255, 255, 0.40 );
    border-radius: 10px;
    transform: scale(1.02);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    
}

.experience-card__heading {
    margin-left: 5px;
    margin-top: 10px;
    font-size: 1.8rem;
    font-weight: 500;
    color: #354358;
    text-decoration: underline;
}

.experience-card__name {
    font-size: 16px;
}

.experience-card__body {
    margin-top: 10px;
}

.experience-card__title {
    font-weight: 700;
    padding-top: 10px;
}

.experience-card__details {
    font-size: 0.9rem;
    /* padding-top: 20px; */
}

@media (max-width: 600px) {
    .app__main-content {
        margin-right: 10px;
        margin-left: 10px;
    }
    .navbar__active {
        font-size: 1.5rem;
    }

    .navbar__item {
        font-size: 1rem;
    }
}

